import "./styles.css";
import menu from "./menu";

import * as React from "react";

import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

import Checkbox from "@mui/material/Checkbox";
const label = { inputProps: { "aria-label": "Checkbox demo" } };

export default function AR() {
  function menuuu(index) {
    const items = [];

    const values = Object.values(menu[index]);
    for (let i = 0; i < values.length; i++) {
      if (i === 0) {
        items.push(<img src={values[i]} alt="s" />);
      } else {
        items.push(<div>{values[i]}</div>);
      }
    }
    return <ul>{items}</ul>;
  }

  return (
    <>
      <div className="check">
        <Checkbox {...label} />
      </div>
      <div>
        <Accordion className="AAA1">
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>アロウズ</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <div className="ll">
              <div className="Kaigyo">
                <div className="Gun">{menuuu(119)}</div>
                <div className="Gun">{menuuu(120)}</div>
                <div className="Gun">{menuuu(121)}</div>
              </div>
            </div>
          </AccordionDetails>
        </Accordion>
      </div>
      <div className="check">
        <Checkbox {...label} />
      </div>
      <div>
        <Accordion className="AAA1">
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>GAT-X</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <div className="ll">
              <div className="Kaigyo">
                <div className="Gun">{menuuu(122)}</div>
                <div className="Gun">{menuuu(123)}</div>
              </div>
            </div>
          </AccordionDetails>
        </Accordion>
      </div>
      <div className="check">
        <Checkbox {...label} />
      </div>
      <div>
        <Accordion className="AAA1">
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>ZGMF-X</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <div className="ll">
              <div className="Kaigyo">
                <div className="Gun">{menuuu(124)}</div>
                <div className="Gun">{menuuu(125)}</div>
                <div className="Gun">{menuuu(126)}</div>
                <div className="Gun">{menuuu(127)}</div>
                <div className="Gun">{menuuu(128)}</div>
                <div className="Gun">{menuuu(129)}</div>
              </div>
            </div>
          </AccordionDetails>
        </Accordion>
      </div>
      <div className="check">
        <Checkbox {...label} />
      </div>
      <div>
        <Accordion className="AAA1">
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>モビルファイター</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <div className="ll">
              <div className="Kaigyo">
                <div className="Gun">{menuuu(130)}</div>
                <div className="Gun">{menuuu(131)}</div>
                <div className="Gun">{menuuu(132)}</div>
                <div className="Gun">{menuuu(133)}</div>
                <div className="Gun">{menuuu(134)}</div>
              </div>
            </div>
          </AccordionDetails>
        </Accordion>
      </div>
      <div className="check">
        <Checkbox {...label} />
      </div>
      <div>
        <Accordion className="AAA1">
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>アナザー</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <div className="ll">
              <div className="Kaigyo">
                <div className="Gun">{menuuu(135)}</div>
                <div className="Gun">{menuuu(136)}</div>
              </div>
            </div>
          </AccordionDetails>
        </Accordion>
      </div>
    </>
  );
}
