import "./styles.css";
import menu from "./menu";

import * as React from "react";

import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

import Checkbox from "@mui/material/Checkbox";
const label = { inputProps: { "aria-label": "Checkbox demo" } };

export default function GPS() {
  function menuuu(index) {
    const items = [];

    const values = Object.values(menu[index]);
    for (let i = 0; i < values.length; i++) {
      if (i === 0) {
        items.push(<img src={values[i]} alt="s" />);
      } else {
        items.push(<div>{values[i]}</div>);
      }
    }
    return <ul>{items}</ul>;
  }

  return (
    <>
      <div className="check">
        <Checkbox {...label} />
      </div>
      <div>
        <Accordion className="AAA3">
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>GPシリーズ</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <div className="ll">
              <div className="Kaigyo">
                <div className="Gun">{menuuu(39)}</div>
                <div className="Gun">{menuuu(40)}</div>
                <div className="Gun">{menuuu(41)}</div>
                <div className="Gun">{menuuu(42)}</div>
              </div>
            </div>
          </AccordionDetails>
        </Accordion>
      </div>
      <div className="check">
        <Checkbox {...label} />
      </div>
      <div>
        <Accordion className="AAA1">
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>Z計画</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <div className="ll">
              <div className="Kaigyo">
                <div className="Gun">{menuuu(43)}</div>
                <div className="Gun">{menuuu(44)}</div>
                <div className="Gun">{menuuu(45)}</div>
                <div className="Gun">{menuuu(46)}</div>
                <div className="Gun">{menuuu(47)}</div>
                <div className="Gun">{menuuu(48)}</div>
                <div className="Gun">{menuuu(49)}</div>
              </div>
            </div>
          </AccordionDetails>
        </Accordion>
      </div>
      <div className="check">
        <Checkbox {...label} />
      </div>
      <div>
        <Accordion className="AAA1">
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>ティターンズ</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <div className="ll">
              <div className="Kaigyo">
                <div className="Gun">{menuuu(50)}</div>
                <div className="Gun">{menuuu(51)}</div>
                <div className="Gun">{menuuu(52)}</div>
                <div className="Gun">{menuuu(53)}</div>
              </div>
            </div>
          </AccordionDetails>
        </Accordion>
      </div>
      <div className="check">
        <Checkbox {...label} />
      </div>
      <div>
        <Accordion className="AAA2">
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>アクシズ</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <div className="ll">
              <div className="Kaigyo">
                <div className="Gun">{menuuu(54)}</div>
                <div className="Gun">{menuuu(55)}</div>
                <div className="Gun">{menuuu(56)}</div>
              </div>
            </div>
          </AccordionDetails>
        </Accordion>
      </div>
      <div className="check">
        <Checkbox {...label} />
      </div>
      <div>
        <Accordion className="AAA2">
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>ネオ・ジオン</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <div className="ll">
              <div className="Kaigyo">
                <div className="Gun">{menuuu(57)}</div>
                <div className="Gun">{menuuu(58)}</div>
                <div className="Gun">{menuuu(59)}</div>
                <div className="Gun">{menuuu(60)}</div>
                <div className="Gun">{menuuu(61)}</div>
                <div className="Gun">{menuuu(62)}</div>
                <div className="Gun">{menuuu(63)}</div>
                <div className="Gun">{menuuu(64)}</div>
                <div className="Gun">{menuuu(65)}</div>
                <div className="Gun">{menuuu(66)}</div>
                <div className="Gun">{menuuu(67)}</div>
                <div className="Gun">{menuuu(68)}</div>
                <div className="Gun">{menuuu(69)}</div>
              </div>
            </div>
          </AccordionDetails>
        </Accordion>
      </div>
    </>
  );
}
